@use "../base/variables" as v;
@use "../base/mixins" as m;

.contenedor-app {
  @include m.tablet {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    min-height: 100vh;
  }

  .imagen {
    background-image: url("../img/karsten-winegeart-hir6uNh8Jpc-unsplash.jpg");
    background-size: cover;
    background-position: center center;
    background-repeat: no-repeat;
    height: auto;
  }

  .app {
    max-width: 90%;
    min-width: 85%;
    margin: 0 auto;
    padding: 3rem 0;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
  }
}


.eliminar{
  &:hover{
    color: rgb(238, 21, 21);
  }
}

.actualizar{
  &:hover{
    color: rgb(0, 26, 255);
  }
}

.btn-crear{
  margin-top: 1rem;
  font-size: 1.8rem;
}