@use "../base/variables" as v;
@use "../base/mixins" as m;

.citas {
  overflow-y: auto;
  max-width: 95%;
  margin: auto;
  height: 48rem;
  padding: 0;

  li {
    list-style: none;
    border-bottom: 1px solid black;
  }
  p {
    font-size: 1.6rem;
  }
}

.titulo__servicios {
  font-weight: v.$medium;
}

.total_citas {
  font-weight: v.$medium;
}

.alerta_sin_citas {
  text-align: center;
  font-size: 2rem;
}
.contenido_admin {
  max-height: 80rem;

  position: relative;
  bottom: 1rem;
}

.formulario__admin {
  margin: auto;
}

.btn__eliminar {
  margin: 1.5rem auto;
  padding: 0.5rem;
  border: 0;
  cursor: pointer;
  min-width: 70%;
  background-color: rgb(228, 41, 41);
  color: v.$blanco;
  border-radius: 0.4rem;
}

.formulario__botones {
  display: flex;
  justify-content: center;
}

.btn_panel--admin {
  padding: 0rem;
  text-align: center;
  width: 100%;
  border: none;
  cursor: pointer;
  border-radius: 1.6rem;
  transition: all 0.25s;
  padding: 0.7rem;
  @include m.desktop {
    padding: 1rem;
  }
  color: #000;
  &:hover {
    background-color: #000;
    color: #fff;
  }
}

.contenedor_cita--admin {
  min-width: 85%;
  height: 97%;

  @include m.tablet {
    height: auto;
  }
}

.tabs--admin {
  min-width: 99%;
}

.formulario--admin {
  width: 90%;
  margin: auto;
}

.servicio--admin {
  p {
    font-size: 1.6rem;
    font-weight: v.$medium;
  }

  span {
    font-weight: v.$regular;
  }

  border-bottom: 1px solid v.$negro;
}

.categoria-servicio {
  margin: 2rem 0 1rem;
  font-size: 1.8rem;
}

.contendor--servicios {
  overflow-y: auto;
  max-width: 95%;
  margin: auto;
  height: 59rem;
  padding: 0;
}

.acciones {
  min-height: 4rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  a,
  input {
    margin: 0 2rem 1rem;
    background-color: white;
    border: none;
    color: v.$negro;
  }
}
