@use '../base/variables' as v;
@use '../base/mixins' as m;

.contenedor_cita{
min-width: 85%;
height: 97%;

@include m.tablet{
    height: auto;
}

}

.listado-servicios{
 max-height: 25rem;

 @include m.tablet{
    max-height: 45rem;
 }
 overflow-y: auto;   
 max-width: 95%;
 margin: auto;
}

.servicio{
    display: flex;
    cursor: pointer;
    justify-content: space-between;
    transition: background-color 400ms;
    &:hover{
        background-color: rgb(185, 195, 195);
        font-weight: v.$medium;
      
    }
}

.seleccionado{
    background-color: rgb(185, 195, 195);
    font-weight: v.$medium;
}

.categoria-servicio{
    font-weight: v.$medium;
    text-align: center;
    text-transform: uppercase;
}

.nombre-servicio{
    font-size: 1.6rem;
    display: block;
}

.descripcion__servicios{
    margin-bottom: 2.5rem;
}