@use 'variables' as v;

html {
    font-size: 62.5%;
    box-sizing: border-box;
    height: 100%;
}
body {
    min-height: 100%;
    display: flex;
    flex-direction: column;
    background-color: v.$blanco;
    color: v.$negro;
}
*, *:before, *:after {
    box-sizing: inherit;
}
body {
    font-family: v.$fuente_principal;
    font-size: 1.6rem;
}
.contenedor {
    width: 95%;
    max-width: 1200px;
    margin: 0 auto;
}
a {
    text-decoration: none;
}

img {
    max-width: 100%;
}
.label{
    display: block;
    margin: 2rem 0 .2rem;
}

.input{
    display: block;
    width: 99%;
    font-size: 1.7rem;
    padding: .5rem;
    margin: 0;
    border: none;
    border-bottom: 2px solid v.$negro;
}

input:focus {
    outline: none;
  }

  button{
    cursor: pointer;
  }

.center{
    margin: 0 auto;
    width: 100%;
}

.rbg-red {
    display: none;
    font-size: 1.2rem;
    color: rgb(231, 79, 79);
  }
  
  .rbg-green {
    display: none;
    font-size: 1.2rem;
    color: rgb(27, 146, 43);
  }
  
  .alerta {
    display: block;
  }

  
  .alerta-center{
    text-align: center;
    font-size: 1.6rem;
  }