@use "../base/variables" as v;
@use "../base/mixins" as m;

.formulario {
  max-width: 100rem;

  @include m.tablet {
    width: 90%;
  }


  padding: 2rem;
  &__bienvenida {
    color: v.$negro;
    font-size: 3.9rem;
    margin: 0;
    text-align: center;
  }

  &__descripcion {
    margin: 2rem 0 2rem;
    font-weight: v.$extra-delgada;
    font-size: 1.85rem;
    color: v.$negro;
  }
}

.olvide_pass {
  display: block;
  margin: 1.5rem 0 0 0;
  color: #5050e3;
}

.boton {
  width: 100%;
  padding: 1.2rem;
  background-color: v.$negro;
  font-weight: 400;
  border-radius: 1rem;
  border: none;
  font-weight: v.$medium;
  color: v.$blanco;
  margin-top: 1rem;
}

.crear-cuenta {
  font-size: 1.5rem;
}

.row {
  display: flex;
  align-items: center;
  font-size: 2rem;
  color: v.$negro;
}

.contenedor__back {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  margin-top: 1.5rem;
}

.back{
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}

.input-incorrecto {
  border-bottom: 2px solid rgb(233, 44, 44);
  animation: sacudir 0.8s ease-in-out;
}

@keyframes sacudir {
  10%,
  90% {
    transform: translate3d(-2px, 0, 0);
  }

  20%,
  80% {
    transform: translate3d(2px, 0, 0);
  }

  30%,
  50%,
  70% {
    transform: translate3d(-2px, 0, 0);
  }

  40%,
  60% {
    transform: translate3d(2px, 0, 0);
  }
}


.generar-token {
  display: block;
  margin: 0;
  color: #5050e3;
  border: 0px;
  background-color: unset;
}


