html {
  font-size: 62.5%;
  box-sizing: border-box;
  height: 100%;
}

body {
  min-height: 100%;
  display: flex;
  flex-direction: column;
  background-color: #FFFFFF;
  color: #1a1b15;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Poppins", sans-serif;
  font-size: 1.6rem;
}

.contenedor {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
}

.label {
  display: block;
  margin: 2rem 0 0.2rem;
}

.input {
  display: block;
  width: 99%;
  font-size: 1.7rem;
  padding: 0.5rem;
  margin: 0;
  border: none;
  border-bottom: 2px solid #1a1b15;
}

input:focus {
  outline: none;
}

button {
  cursor: pointer;
}

.center {
  margin: 0 auto;
  width: 100%;
}

.rbg-red {
  display: none;
  font-size: 1.2rem;
  color: rgb(231, 79, 79);
}

.rbg-green {
  display: none;
  font-size: 1.2rem;
  color: rgb(27, 146, 43);
}

.alerta {
  display: block;
}

.alerta-center {
  text-align: center;
  font-size: 1.6rem;
}

h1, h2, h3 {
  font-family: "Poppins", sans-serif;
  margin: 0 0 2.5rem 0;
  font-weight: 900;
  color: #1a1b15;
  text-align: center;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3rem;
  text-align: center;
}

p {
  color: #1a1b15;
  font-size: 2rem;
}

@media (min-width: 768px) {
  .contenedor-app {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    min-height: 100vh;
  }
}
.contenedor-app .imagen {
  background-image: url("../img/karsten-winegeart-hir6uNh8Jpc-unsplash.jpg");
  background-size: cover;
  background-position: center center;
  background-repeat: no-repeat;
  height: auto;
}
.contenedor-app .app {
  max-width: 90%;
  min-width: 85%;
  margin: 0 auto;
  padding: 3rem 0;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
}

.eliminar:hover {
  color: rgb(238, 21, 21);
}

.actualizar:hover {
  color: rgb(0, 26, 255);
}

.btn-crear {
  margin-top: 1rem;
  font-size: 1.8rem;
}

.contenedor_cita {
  min-width: 85%;
  height: 97%;
}
@media (min-width: 768px) {
  .contenedor_cita {
    height: auto;
  }
}

.listado-servicios {
  max-height: 25rem;
  overflow-y: auto;
  max-width: 95%;
  margin: auto;
}
@media (min-width: 768px) {
  .listado-servicios {
    max-height: 45rem;
  }
}

.servicio {
  display: flex;
  cursor: pointer;
  justify-content: space-between;
  transition: background-color 400ms;
}
.servicio:hover {
  background-color: rgb(185, 195, 195);
  font-weight: 600;
}

.seleccionado {
  background-color: rgb(185, 195, 195);
  font-weight: 600;
}

.categoria-servicio {
  font-weight: 600;
  text-align: center;
  text-transform: uppercase;
}

.nombre-servicio {
  font-size: 1.6rem;
  display: block;
}

.descripcion__servicios {
  margin-bottom: 2.5rem;
}

.paginacion {
  display: flex;
  justify-content: space-between;
  margin-top: 2rem;
}

.formulario {
  max-width: 100rem;
  padding: 2rem;
}
@media (min-width: 768px) {
  .formulario {
    width: 90%;
  }
}
.formulario__bienvenida {
  color: #1a1b15;
  font-size: 3.9rem;
  margin: 0;
  text-align: center;
}
.formulario__descripcion {
  margin: 2rem 0 2rem;
  font-weight: 200;
  font-size: 1.85rem;
  color: #1a1b15;
}

.olvide_pass {
  display: block;
  margin: 1.5rem 0 0 0;
  color: #5050e3;
}

.boton {
  width: 100%;
  padding: 1.2rem;
  background-color: #1a1b15;
  font-weight: 400;
  border-radius: 1rem;
  border: none;
  font-weight: 600;
  color: #FFFFFF;
  margin-top: 1rem;
}

.crear-cuenta {
  font-size: 1.5rem;
}

.row {
  display: flex;
  align-items: center;
  font-size: 2rem;
  color: #1a1b15;
}

.contenedor__back {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  margin-top: 1.5rem;
}

.back {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}

.input-incorrecto {
  border-bottom: 2px solid rgb(233, 44, 44);
  animation: sacudir 0.8s ease-in-out;
}

@keyframes sacudir {
  10%, 90% {
    transform: translate3d(-2px, 0, 0);
  }
  20%, 80% {
    transform: translate3d(2px, 0, 0);
  }
  30%, 50%, 70% {
    transform: translate3d(-2px, 0, 0);
  }
  40%, 60% {
    transform: translate3d(2px, 0, 0);
  }
}
.generar-token {
  display: block;
  margin: 0;
  color: #5050e3;
  border: 0px;
  background-color: unset;
}

.text_info {
  font-size: 1.8rem;
  margin: 0;
  text-align: center;
}

.sub_titulo {
  text-align: center;
  font-weight: 400;
  margin: 1.5rem 0 1rem;
}

.titulo_cita {
  color: #1a1b15;
  font-size: 3rem;
  margin-bottom: 1rem;
  text-align: center;
  font-weight: 400;
}
@media (min-width: 480px) {
  .titulo_cita {
    font-size: 3.9rem;
  }
}

.descripcion {
  text-align: center;
  margin-bottom: 1.2rem;
}

.seccion {
  display: none;
}

.mostrar {
  display: block;
}

.ocultar {
  display: none;
}

.visibility-hidden {
  visibility: hidden;
}

.tabs {
  border-radius: 1.6rem;
  border: 1px solid rgb(188, 188, 188);
  display: flex;
  justify-content: center;
  margin-bottom: 2rem;
}
.tabs button {
  padding: 0rem;
  width: 100%;
  border: none;
  cursor: pointer;
  border-radius: 1.6rem;
  transition: all 0.25s;
  padding: 0.7rem;
}
@media (min-width: 1024px) {
  .tabs button {
    padding: 1rem;
  }
}
.tabs button:hover {
  background-color: #000;
  color: #fff;
}

.desactivado {
  background-color: white;
  color: #000;
}

.activo {
  background-color: #000;
  color: #fff;
}

.btn--border {
  background-color: unset;
  border: none;
  transition: border-bottom 0.3s;
  border-bottom: 1.5px solid #FFFFFF;
}
.btn--border:hover {
  border-bottom: 1.5px solid #1a1b15;
}

.contenedor__btn {
  display: flex;
  justify-content: end;
  width: 99%;
}

.btn__sing-off {
  font-weight: 400;
  color: #1a1b15;
  font-size: 1.85rem;
  text-align: end;
  margin-right: 2.5rem;
}

.contenedor-servicios {
  border-bottom: 1.5px solid #1a1b15;
  display: flex;
  justify-content: space-between;
}

.resumen {
  background-color: rgb(226, 225, 225);
  padding: 0.5rem 1rem;
  border-radius: 0.5rem;
  overflow-y: auto;
  margin: 2rem auto;
  max-height: 40rem;
}
.resumen p {
  margin-bottom: 1rem;
  font-size: 1.8rem;
}

.dato {
  text-align: center;
}

.total {
  text-align: end;
}

.citas {
  overflow-y: auto;
  max-width: 95%;
  margin: auto;
  height: 48rem;
  padding: 0;
}
.citas li {
  list-style: none;
  border-bottom: 1px solid black;
}
.citas p {
  font-size: 1.6rem;
}

.titulo__servicios {
  font-weight: 600;
}

.total_citas {
  font-weight: 600;
}

.alerta_sin_citas {
  text-align: center;
  font-size: 2rem;
}

.contenido_admin {
  max-height: 80rem;
  position: relative;
  bottom: 1rem;
}

.formulario__admin {
  margin: auto;
}

.btn__eliminar {
  margin: 1.5rem auto;
  padding: 0.5rem;
  border: 0;
  cursor: pointer;
  min-width: 70%;
  background-color: rgb(228, 41, 41);
  color: #FFFFFF;
  border-radius: 0.4rem;
}

.formulario__botones {
  display: flex;
  justify-content: center;
}

.btn_panel--admin {
  padding: 0rem;
  text-align: center;
  width: 100%;
  border: none;
  cursor: pointer;
  border-radius: 1.6rem;
  transition: all 0.25s;
  padding: 0.7rem;
  color: #000;
}
@media (min-width: 1024px) {
  .btn_panel--admin {
    padding: 1rem;
  }
}
.btn_panel--admin:hover {
  background-color: #000;
  color: #fff;
}

.contenedor_cita--admin {
  min-width: 85%;
  height: 97%;
}
@media (min-width: 768px) {
  .contenedor_cita--admin {
    height: auto;
  }
}

.tabs--admin {
  min-width: 99%;
}

.formulario--admin {
  width: 90%;
  margin: auto;
}

.servicio--admin {
  border-bottom: 1px solid #1a1b15;
}
.servicio--admin p {
  font-size: 1.6rem;
  font-weight: 600;
}
.servicio--admin span {
  font-weight: 400;
}

.categoria-servicio {
  margin: 2rem 0 1rem;
  font-size: 1.8rem;
}

.contendor--servicios {
  overflow-y: auto;
  max-width: 95%;
  margin: auto;
  height: 59rem;
  padding: 0;
}

.acciones {
  min-height: 4rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.acciones a,
.acciones input {
  margin: 0 2rem 1rem;
  background-color: white;
  border: none;
  color: #1a1b15;
}