@use "../base/variables" as v;
@use "../base/mixins" as m;

.text_info {
  font-size: 1.8rem;
  margin: 0;
  text-align: center;
}

.sub_titulo {
  text-align: center;
  font-weight: v.$regular;
  margin: 1.5rem 0 1rem;
}

.titulo_cita {
  color: v.$negro;
  font-size: 3rem;
  margin-bottom: 1rem;
  text-align: center;
  font-weight: v.$regular;

  @include m.telefono {
    font-size: 3.9rem;
  }
}

.descripcion {
  text-align: center;
  margin-bottom: 1.2rem;
}

.seccion {
  display: none;
}

.mostrar {
  display: block;
}

.ocultar {
  display: none;
}

.visibility-hidden {
  visibility: hidden;
}

.tabs {
  border-radius: 1.6rem;
  border: 1px solid rgb(188, 188, 188);
  display: flex;
  justify-content: center;
  margin-bottom: 2rem;
  button {
    padding: 0rem;
    width: 100%;
    border: none;
    cursor: pointer;
    border-radius: 1.6rem;
    transition: all 0.25s;
    padding: .7rem;
    @include m.desktop {
      padding: 1rem;
    }

    &:hover {
      background-color: #000;
      color: #fff;
    }
  }
}

.desactivado {
  background-color: white;
  color: #000;
}

.activo {
  background-color: #000;
  color: #fff;
}

.btn--border {
  background-color: unset;
  border: none;
  transition: border-bottom 0.3s;
  border-bottom: 1.5px solid v.$blanco;
  &:hover {
    border-bottom: 1.5px solid v.$negro;
  }
}


.contenedor__btn{
  display: flex;
  justify-content: end;
  width: 99%;

}

.btn__sing-off{
  font-weight: v.$regular;
  color: v.$negro;
  font-size: 1.85rem;
  text-align: end;
  margin-right: 2.5rem;

}